import type { Component, ExtractPropTypes, PropType } from 'vue'
import type { IType } from './message.type'

export const definePropType = <T>(val: any): PropType<T> => val

export const iconPropType = definePropType<string | Component>([
  String,
  Object,
  Function
])

export const messageProps = {
  id: {
    type: String,
    default: ''
  },
  message: {
    type: String,
    default: ''
  },
  type: {
    type: String as PropType<IType>,
    default: 'success'
  },
  center: {
    type: Boolean,
    default: false
  },
  duration: {
    type: Number,
    default: 3000
  },
  showClose: {
    type: Boolean,
    default: false
  },
  dangerouslyUseHTMLString: {
    type: Boolean,
    default: false
  },
  offset: {
    type: Number,
    default: 16
  },
  onClose: {
    type: Function as PropType<() => void>,
    require: false
  },
  icon: {
    type: iconPropType,
    default: ''
  }
} as const

export type MessageProps = ExtractPropTypes<typeof messageProps>

interface iconTypes {
  success?: string
  error?: string
}

export const iconMaps: iconTypes = {
  success: 'as-ui-shenqingchenggong',
  error: 'as-ui-quxiao'
}
